import bankCodesConfig from './bank-codes-config';

/**
 * Given an iban returns that country specification
 *
 * @type {Function}
 * @inner
 * @throws {Error} If Country code is not defined
 * @param {String} iban A valid IBAN number
 * @return {Object} Country specification
 */
export default (iban) => {
  const countryCode = iban.slice(0, 2);
  const bankCode = iban.slice(4, 8);

  if (!Object.hasOwnProperty.call(bankCodesConfig, countryCode)) {
    throw new Error(`Country code ${countryCode} is not supported`);
  }


  if (bankCode) {
    return bankCodesConfig[countryCode].filter(bank => bank.code.startsWith(bankCode));
  }

  return bankCodesConfig[countryCode].filter(bank => bank.default);
};



